/* forum-regular - cyrillic */
@font-face {
  font-family: 'Forum';
  font-style: normal;
  font-weight: 400;
  src: local("Forum Regular"), url("../fonts/forum/forum-v11-cyrillic-regular.woff2") format("woff2"), url("../fonts/forum/forum-v11-cyrillic-regular.woff") format("woff");
  /* Modern Browsers */
  font-display: swap; }

h1 {
  color: red; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  padding: 0;
  color: #ffffff;
  background-color: #3B181E;
  font-family: "Forum", "Georgia", serif;
  font-weight: 400;
  font-size: 18px;
  font-style: normal;
  line-height: 120%; }

a {
  text-decoration: none;
  color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%); }

.button {
  background-color: transparent;
  text-align: center;
  border: none;
  cursor: pointer; }

.page-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  min-height: 100%;
  border: 1px solid #ffffff; }

.body--modal-open {
  height: 100vh;
  overflow: hidden; }

.page-header__logo-icon {
  fill: none;
  stroke: #ffffff; }

.main {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto; }

.page-footer {
  width: 100%;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto; }
